/**
 *   Name: BizAgi Render Image Class
 *   Author: Bizagi Mobile Team
 *   Comments:
 *   -   This script will redefine the image render class to adjust to tablet devices
 */
// Extends itself
//noinspection JSValidateTypes
bizagi.rendering.image.extend("bizagi.rendering.image", {
  QUALITY_PICTURE: 50,
  EXTENSIONSIMG: ["jpeg", "jpg", "png"] },
{
  /**
   * Just listing the order of function calls (bizagi.rendering.render)
   * 1. renderControl (found it on bizagi.rendering.image)
   * 2. postRender / postRenderReadOnly / postRenderPrintVersion
   * 3. configureHandlers / configureHandlersReadOnly
   *
   * Other functions are just local implementation
   *
   */

  /**
       * DOM elements to use as an ADD option button
       * @var $actionImage jQuery DOM
       */
  $addImage: null,
  /**
                    * DOM elements to use as a REMOVE option button
                    * @var $actionImage jQuery DOM
                    */
  $deleteImage: null,
  /**
                       * DOM input to handle HTML uploads
                       * @var $inputFile jQuery DOM
                       */
  $inputFile: null,
  /**
                     * Whether or not is a native device 'Just a shortcut
                     * @var isNative boolean
                     */
  isNative: false,
  /**
                    * Whether or not we are offline 'Just a shortcut
                    * @var isOfflineForm boolean
                    */
  isOfflineForm: false,
  /**
                         * resolution options to show in menu after selecting an image
                         * @var resolutionUpload Array
                         */
  resolutionUpload: [],

  /**
                         * This method tries to get the HTML template an render the current object inside
                         * @return {jQuery} $.Deferred()
                         */
  renderControl: function renderControl() {
    var self = this;

    /**
                      * Check if this is an offline form
                      * It doesn't validate if the device goes offline so..
                      */
    this.isOfflineForm = bizagi.util.isOfflineForm({ context: this });
    //noinspection JSValidateTypes,JSUnresolvedVariable
    this.isNative = bizagi.util.isCordovaSupported();

    if (this.isOfflineForm) {
      var maxFileSizeOfProperty = Number(self.properties.maxSize) || 1048576;
      var maxFileSizeOfUser = typeof BIZAGI_SETTINGS !== "undefined" && typeof BIZAGI_SETTINGS.UploadMaxFileSize !== "undefined" ? Number(BIZAGI_SETTINGS.UploadMaxFileSize) : 1048576;

      self.properties.maxSize = maxFileSizeOfProperty < maxFileSizeOfUser ? maxFileSizeOfProperty : maxFileSizeOfUser;
    }

    var template = self.renderFactory.getTemplate("image");

    var allowDelete = self.isOfflineForm ? false : self.properties.allowDelete && self.properties.editable;
    var html = $.kendoTmpl(template, {
      url: "",
      allowDelete: allowDelete,
      editable: self.properties.editable });


    return html;
  },

  /**
      * There is no difference when rendering a readonly image, so ...
      * @return {jQuery} $.Deferred()
      */
  renderReadOnly: function renderReadOnly() {
    return this.renderControl();
  },

  /**
      * After rendering element
      */
  postRender: function postRender() {
    var self = this;

    self.showAsyncImage();
  },

  /**
      * After rendering a readonly element
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;

    // Just a little colors
    self.element.addClass("bz-rn-read-only bz-rn-non-editable");
    self.showAsyncImage();
  },
  /**
      * Add action handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var version = bizagi.util.getAndroidVersion();
    var modalOptions = null;

    this.resolutionUpload = [];
    this.$addImage = $(".bz-rn-image-actions-add", this.getControl());
    this.$deleteImage = $(".bz-rn-image-actions-delete", this.getControl());
    this.$inputFile = $(".bz-rn-upload-image-web", this.getControl());

    this._super();

    if (this.isNative) {

      this.$addImage.off("click").click(function () {

        var modal = self.createModal();
        // Upload Image Options
        modalOptions = modal.find("ul.bz-rn-image-options");

        // Can't remove from here
        modalOptions.find("li[data-role=\"dimage\"]").remove();

        modal.find("ul.bz-rn-image-options li").bind("click", function () {
          modal.kendoMobileModalView("close");
          switch ($(this).data("role")) {
            case "image":
              self.imageAdd();
              break;
            case "cimage":
              self.imageAdd(true);
              break;
            default:}

        });
        modal.find(".bz-wp-modalview-close").unbind("click").bind("click", function () {
          modal.kendoMobileModalView("close");
        });

        self.triggerGlobalHandler("modalViewDidAppear", {});
        modal.kendoMobileModalView("open");
        modal.adjustSize();

        modal.closest(".km-modalview-root").unbind("click").bind("click", function () {
          modal.kendoMobileModalView("close");
        });

      });

    } else {
      if (bizagi.util.media.fileAPISupported() && !self.isOfflineForm) {
        this.$addImage.off("click").click(function () {
          self.$inputFile.click();
        });
      } else {
        this.element.addClass("bz-rn-read-only bz-rn-non-editable");
        this.$addImage.removeClass("bz-plus").addClass("bz-rn-read-only");
      }
    }

    // remove Image
    this.$deleteImage.off("click").click(function () {
      self.imageDelete();
    });
    // Image change (Hybrid only)
    this.$inputFile.off("change").on("change", function () {
      self.imageAdd();
      this.value = "";
    });

    // Just in case
    this.$inputFile.off("click").on("click", function (e) {
      e.stopPropagation();
    });

    this.getControl().find("img").off("click").click(function () {
      self.renderEdition();
    });

    // Resolution upload menu
    // If android version is less than 4.4 avoid to show upload sizes due to compatibility with blob files
    if (version && version <= 4.4) {
      resolutionUpload = [
      { "guid": 4, "displayName": bizagi.localization.getResource("workportal-size-original") }];

    } else {
      resolutionUpload = bizagi.util.media.getImageResolutionItems();
    }

    $(".bz-resolution-container", self.getControl()).off("click").actionSheet({
      actions: resolutionUpload,
      actionClicked: function actionClicked(action) {
        bizagi.loading.start();
        $.when(self.resolutionButtonHandler(action.guid)).
        always(function () {
          bizagi.loading.stop();
        });
      },
      appear: function appear(action) {
        self.triggerGlobalHandler("modalViewDidAppear", {});
      },
      cancelClicked: function cancelClicked(action) {
        self.triggerGlobalHandler("modalViewDidDisappear", {});
      } });

  },

  clearDisplayValue: function clearDisplayValue() {
    this.$deleteImage.click();
  },

  /**
      * Render edition view
      */
  renderEdition: function renderEdition() {
    var self = this;
    var properties = self.properties;

    // TODO Creates a container, be careful you must destroy it later
    var baseParams = self.getParams();
    var container = baseParams.navigation.createRenderContainer({
      title: properties.displayName,
      view: {
        zoom: true,
        useNativeScrolling: false } });



    var img = $.kendoTmpls(self.renderFactory.getTemplate("image-preview"), { "url": self.value && self.value[0] !== "" ? self.value[0] : "" });
    container.element.css("display", "none");
    self.inputEdition = container.element.html(img);
    baseParams.navigation.navigate(container.id);

    if (self.isOfflineForm) {
      container.element.on("close", self.onPreviewActionBack.bind(self));
    }

    bizagi.loading.start();

    // TODO review fail scenario, should it call the forms.failHandler method?
    $.when(self.buildFullItemUrl()).done(function (dataUrl) {
      container.element.css("display", "block");

      if (dataUrl.indexOf("error") < 0) {
        $(".bz-rn-image-render-preview", container.element).attr("src", dataUrl);
        var originalImage = $(".bz-rn-image-render-preview", container.element)[0];
        var originalImageWidth = $(originalImage).css("width");
        originalImageWidth = Number(originalImageWidth.substring(0, originalImageWidth.length - 2));

        if (self.properties.width !== -1 && self.properties.width < originalImageWidth) {
          $(".bz-rn-image-render-preview", container.element).css("width", "100%");
        }
      }
    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Refresh data
      * @param {} event      
      */
  onPreviewActionBack: function onPreviewActionBack(event) {
    var self = this;
    var dataUrl = $(".bz-rn-image-render-preview", event.target).attr("src") || "";

    var dataUrlToSave = dataUrl.length > 0 ? [].concat(dataUrl) : null;
    self.setValue(dataUrlToSave);
  },

  /**
      * Creates the modalview based on template
      * @returns {jQuery} kendoMobileModalView
      */
  createModal: function createModal() {
    var self = this;

    var imageTemplate = self.renderFactory.getTemplate("imageModalView");
    var modalViewTemplate = kendo.template(imageTemplate, { useWithBlock: false });

    var modalView = $($.trim(modalViewTemplate({
      editable: self.properties.editable,
      displayName: self.properties.displayName || "",
      orientation: self.properties.orientation || "" })));


    //noinspection JSUnresolvedFunction
    modalView.kendoMobileModalView({
      close: function close(event) {
        event.preventDefault();

        if (window.event && window.event.stopPropagation)
        window.event.stopPropagation();

        self.triggerGlobalHandler("modalViewDidDisappear", {});

        this.destroy();
        this.element.remove();
      },
      show: function show() {

      },
      modal: false });


    // Adjust modal to window size
    modalView.adjustSize = function () {
      var maxHeight = 0;
      var elements = $(this).parent();

      var windowHeightSize = window.innerHeight;
      var windowWidthSize = window.innerWidth;

      var listHeight = $(".bz-wp-modalview-results-wrapper", elements).height();
      var maxWidth = $(".bz-wp-modalview-results-wrapper", elements).width();
      var popupHeight = $("[data-role=header]", elements).height();

      if (listHeight + popupHeight >= windowHeightSize) {
        maxHeight = windowHeightSize - 20;
      } else {
        maxHeight = listHeight + popupHeight + 10;
      }

      var top = (windowHeightSize - maxHeight) / 2;
      var left = (windowWidthSize - maxWidth) / 2;

      elements.css("height", maxHeight);

      elements.parent().css("top", top);
      elements.parent().css("left", left);
      elements.parent().css("height", maxHeight);
    };

    return modalView;
  },

  /**
      * Add an image
      * @param useCamera boolean Whether or not to use device's camera
      */
  imageAdd: function imageAdd(useCamera) {
    var self = this;

    if (!this.isNative) {
      var dataFile = this.$inputFile[0].files[0];
      dataFile.fullPath = this.$inputFile[0].value;
      var extensions = this.Class.EXTENSIONSIMG;

      bizagi.loading.start();
      if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) &&
      bizagi.util.media.checkFileTypes(dataFile, self.properties, extensions)) {

        $.when(self.uploadFile(dataFile)).done(function (result) {
          self.uploadFileCompleted(self);
        }).fail(function (error) {
          self.executeFormFailHandler(error);
        }).always(function () {
          bizagi.loading.stop();
        });
      } else {
        bizagi.loading.stop();
      }
    } else {

      var options = {
        quality: self.Class.QUALITY_PICTURE,
        targetWidth: 1280,
        targetHeight: 960,
        correctOrientation: true };


      if (!useCamera) {
        var coordinates = self.getControl()[0].getBoundingClientRect();
        //noinspection JSUnresolvedVariable,JSUnresolvedFunction
        options = {
          quality: self.Class.QUALITY_PICTURE,
          sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
          popoverOptions: new CameraPopoverOptions(coordinates.left, coordinates.top - 20, 200, 100, 4),
          destinationType: navigator.camera.DestinationType.FILE_URI };

      }

      //noinspection JSUnresolvedFunction,JSUnresolvedVariable
      navigator.camera.getPicture(function (dataImage) {
        bizagi.loading.start();

        $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).then(function () {
          return $.when(bizagi.util.media.getFileDataInfo(dataImage)).then(function (fileData) {
            if (bizagi.util.media.checkFileTypes(fileData, self.properties, self.Class.EXTENSIONSIMG)) {
              self.dataImage = dataImage;
              self.fileProperties = fileData;
              // "new Blob" constructor is not supported in android version 4.2.2
              var version = bizagi.util.getAndroidVersion();
              if (version && version <= 4.2 || self.isOfflineForm) {

                return $.when(self.uploadFile(self.dataImage)).done(function (result) {
                  self.uploadFileCompleted(self);
                }).fail(function (error) {
                  self.executeFormFailHandler(error);
                });
              } else {

                if (!bizagi.util.isAndroid()) {
                  return $.when(bizagi.util.media.modalResolutionUpload()).then(function (buttonIndex) {
                    if (buttonIndex <= bizagi.util.media.getImageResolutionItems().length) {
                      return self.resolutionButtonHandler(buttonIndex);
                    }
                  });

                } else {
                  // force click to show actionsheet menu
                  $(".bz-resolution-container", self.getControl()).click();
                }
              }
            }
          });
        }).always(function () {
          bizagi.loading.stop();
        });
      }, self.onFail, options);

    }
  },

  /**
      * Management fail messages
      * @param {} error
      * @returns {}
      */
  onFail: function onFail(error) {
    bizagi.loading.stop();
  },

  /**
      * Remove the current image
      */
  imageDelete: function imageDelete() {
    var self = this;

    bizagi.showConfirmationBox(bizagi.localization.getResource("workportal-actionsheet-upload-remove-photo")).
    done(function () {
      bizagi.loading.start();
      $.when(self.dataService.deleteImage({
        url: self.properties.deleteUrl,
        idRender: self.properties.id,
        xpath: self.properties.xpath,
        xpathContext: self.properties.xpathContext,
        idPageCache: self.properties.idPageCache,
        contexttype: self.properties.contexttype })).
      done(function () {
        self.uploadFileCompleted(self);
      }).fail(function (msg) {
        var message;
        var responseText = msg.responseText;

        try {
          message = JSON.parse(responseText);
          message = message.message || responseText;
        } catch (e) {
          message = responseText;
        }
        bizagi.showMessageBox(message, "Error");

      }).always(function () {
        bizagi.loading.stop();
      });
    });
  },

  /**
      * Send file using HTTP
      * @param dataFile
      * @return {jQuery} $.Deferred()
      */
  uploadFile: function uploadFile(dataFile) {

    var self = this;
    var defer = new $.Deferred();

    if (!this.isOfflineForm) {
      if (this.isNative) {
        //noinspection JSUnresolvedFunction
        var params = {};
        var addParams = self.buildAddParams();
        var fileName = self.fileProperties.name;

        if (self.editedImageURL) {
          params.dataFile = self.editedImageURL;
        } else if (self.fileProperties) {
          params.dataFile = self.fileProperties.url;
        }

        addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());
        params.data = addParams;
        params.options = new FileUploadOptions();
        params.options.fileKey = "file";
        params.options.fileName = fileName;
        params.options.mimeType = "image/jpeg";
        params.options.uploadKey = { key: addParams["h_xpath"] };
        params.options.addUrl = self.properties.addUrl;

        //Cleans variables related previous gallery uploads
        self.editedImageURL = null;
        self.fileProperties = null;

        // Fix android 4.4 getting images from recent folder
        params.dataFile = bizagi.util.media.getImagePath(params.dataFile);

        $.when(bizagi.util.media.uploadFile(params)).done(function (result) {
          var response = JSON.parse(decodeURIComponent(result.response));

          if (response.type === "error") {
            defer.reject(response);
          } else {
            defer.resolve(response);
          }
        }).fail(function (error) {
          defer.reject(error);
        }).always(function () {
        });

      } else {
        if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) &&
        bizagi.util.media.checkFileTypes(dataFile, self.properties, self.Class.EXTENSIONSIMG)) {

          self.dataService.processUploadFile({
            url: self.properties.addUrl,
            formData: self.buildAddFileParams(dataFile) }).
          done(function (response) {
            defer.resolve(response);
          }).fail(function (error) {
            defer.reject(error);
          });
        } else {
          defer.reject();
        }
      }
    } else {
      // Load image
      self.saveImageOffline(dataFile, function (dataUrl) {
        var dataUrltoSave = dataUrl.replace(/^data:.*;base64,/, "");

        self.setValue(dataUrltoSave);
        defer.resolve();
      });

      // Enables canbesent on offline mode
      self.canBeSent = function () {
        $.forceCollectData = true;
        return true;
      };
    }

    return defer.promise();
  },

  /**
      * File upload is done
      * @param context
      */
  uploadFileCompleted: function uploadFileCompleted(context) {
    var self = context;

    try {
      self.showAsyncImage();
      self.editedImageURL = null;
    } catch (error) {
      self.getFormContainer().refreshForm();
    }
  },

  /**
      * Show aync image
      * */
  showAsyncImage: function showAsyncImage() {
    var self = this;
    var control = self.getControl();

    $.when(self.buildItemUrl()).done(function (dataUrl) {
      if (!self.isDisposed()) {
        self.properties.url = dataUrl !== "" ? dataUrl : "";
        self.properties.value = self.properties.url.length > 0 ? [].concat(self.properties.url) : null;
        self.setValue(self.properties.value);

        if (self.isOfflineForm && dataUrl.indexOf("Invalid case id") !== -1) {
          self.properties.url = "";
        }

        control.find(".bz-rn-image-container").off("click").
        toggleClass("bz-rn-image-actions-add", self.properties.editable && self.properties.url === "");

        if (self.properties.url !== "") {
          control.find(".bz-rn-image-container img").css("display", "inherit").attr("src", self.properties.url);
          control.find(".bz-rn-image-container img").off("click").click(function () {
            self.renderEdition();
          });
          control.find(".bz-rn-image-actions-add").toggle(self.properties.editable);
          control.find(".bz-rn-image-actions-delete").toggle(self.properties.allowDelete && self.properties.
          editable);
        } else {
          control.find(".bz-rn-image-container img").css("display", "none");
          control.find(".bz-rn-image-actions-add.bz-upload").hide();
          control.find(".bz-rn-image-actions-delete").hide();
        }

        if (self.properties.required) {
          self.getControl().
          children(".ui-bizagi-render-control-required, .ui-bizagi-render-control-required-rtl").
          addClass("image-required");
        }
        self.configureHandlers();
      }
    }).fail(function () {
    });
  },

  /**
      * Create an image offline
      * @param context
      * @param url
      * @param callback
      */
  saveImageOffline: function saveImageOffline(path, callback) {
    var self = this;

    // Fix android 4.4 getting images from recent folder        
    var dataImage = bizagi.util.media.getImagePath(path);
    var xhr = new XMLHttpRequest();

    try {

      xhr.onload = function () {
        var reader = new FileReader();

        reader.onloadend = function () {
          var content = reader.result;
          callback(content);
        };
        reader.readAsDataURL(xhr.response);
      };

      xhr.open("GET", dataImage);
      xhr.responseType = "blob";
      xhr.send();

    } catch (error) {
      callback("");
    }
  },

  /**
      * resolutionButtonHandler
      * @param buttonOrdinal
      */
  resolutionButtonHandler: function resolutionButtonHandler(ordinal) {
    var self = this;
    var actionSelected = ordinal || 0;

    // If original, save image normally, if not, reduce quality and size
    if (actionSelected === bizagi.util.media.ResolutionType.ORIGINAL) {
      return $.when(self.uploadFile(self.dataImage)).done(function (result) {
        self.uploadFileCompleted(self);
      }).fail(function (error) {
        self.executeFormFailHandler(error);
      });

    } else {
      var resolution = bizagi.util.media.getImageResolution(actionSelected);
      return $.when(bizagi.util.media.transformImageSize(self.dataImage, resolution)).
      then(function (result) {

        self.editedImageURL = result.localUrl;
        self.properties.url = result.base64Image;

        return $.when(self.uploadFile(self.dataImage)).
        done(function (result) {
          self.uploadFileCompleted(self);
        }).fail(function (error) {
          self.executeFormFailHandler(error);
        });

      });
    }

  },

  /**
      * Call the form failhandler with an specific error
      * @param {} error
      * @returns {}
      */
  executeFormFailHandler: function executeFormFailHandler(error) {
    var self = this;
    var errorResponse = error.responseText ? error.responseText : bizagi.localization.getResource("workportal-general-error");
    self.getFormContainer().failHandler(errorResponse);
  },

  /**
      * Build add params to send to the server
      * @param {} dataFile
      * @returns {}
      */
  buildAddFileParams: function buildAddFileParams(dataFile) {
    var self = this;
    var properties = self.properties;

    // Get form data for POSTing
    var formData = new FormData();
    var form = self.getFormContainer();

    // Build add params to send to the server
    formData.append(self.Class.BA_CONTEXT_PARAMETER_PREFIX + "xpath", properties.xpath);
    formData.append(self.Class.BA_CONTEXT_PARAMETER_PREFIX + "idRender", properties.id);
    formData.append(self.Class.BA_CONTEXT_PARAMETER_PREFIX + "xpathContext", properties.xpathContext);
    formData.append(self.Class.BA_CONTEXT_PARAMETER_PREFIX + self.Class.BA_PAGE_CACHE, properties.idPageCache);
    formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);
    formData.append(self.Class.BA_CONTEXT_PARAMETER_PREFIX + "action", "savefile");
    formData.append(properties.xpath, dataFile);

    if (self.properties.contexttype) {
      formData.append(self.Class.BA_CONTEXT_PARAMETER_PREFIX + "contexttype", properties.contexttype);
    }

    try {
      if (typeof BIZAGI_SESSION_NAME !== "undefined") {
        formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
      } else {
        formData.append("JSESSIONID", form.properties.sessionId);
      }
    } catch (e) {
      formData.append("JSESSIONID", form.properties.sessionId);
    }

    return formData;
  } });