/*
*   Name: BizAgi Tablet Render upload Extension
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This script will redefine the label render class to adjust to smartphones devices
*/

// Extends itself
bizagi.rendering.upload.extend("bizagi.rendering.upload.offline", {
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
  QUALITY_PICTURE: 50,
  LIMIT: 1, // limit: The maximum number of multimedia files in the device user can record in a single capture operation.
  EXTENSIONSIMG: ["image/jpeg", "jpeg", "image", "png", "jpg", "bmp"],
  EXTENSIONSFILE: ["doc", "docx", "xls", "xlsx", "ppt", "pptx", "pdf", "zip", "rar", "txt"],
  EXTENSIONSVIDEO: ["video/quicktime", "quicktime", "qt", "mov"],
  EXTENSIONSAUDIO: ["audio/wav", "audio", "wav", "mp3"] },
{
  postRender: function postRender() {
    var self = this;

    if (!bizagi.util.isCordovaSupported() || !self.properties.editable) {
      self.addEventToOpenSlide();
    } else {
      self.activateUploadNative();
    }



    // Attach Events
    self.atachOpenFileEvent();
    self.configureHelpText();
  },

  /**
      * Attach open slide
      * @returns {} 
      */
  addEventToOpenSlide: function addEventToOpenSlide() {
    this.itemAddfile = $(".bz-rn-upload-show-menu", this.getControl());
    this.itemAddfile.hide();
  },

  atachOpenFileEvent: function atachOpenFileEvent() {
    var self = this;
    // Handler to open or download the file
    $("ul.files", self.getControl()).on("click", ".ui-bizagi-render-link.button", function (e) {

      var dataIndex = $(this).data("index");
      var selectedValue = typeof dataIndex == "number" ? self.properties.value[dataIndex] : null;

      if (selectedValue) {
        var fileData = selectedValue[1] ? selectedValue[1].value : "";
        var fileMimeType = selectedValue[2] ? selectedValue[2].value : "image/jpeg";

        bizagi.loading.start();
        $.when(bizagi.util.media.openOfflineFile(fileData, fileMimeType, this.title)).fail(function (error) {
          bizagi.showMessageBox(self.getResource("workportal-widget-admin-language-error"));
        }).always(function () {
          bizagi.loading.stop();
        });
      }
    });
  },

  /**  
      *  Template method to implement in each children to customize each control
      * @returns {} 
      */
  renderControl: function renderControl() {
    var self = this;
    var allow_upload = true;
    var items = "";
    var template = self.renderFactory.getTemplate("upload");

    // Check files extensions
    self.checkExtensions();

    if (bizagi.util.isCordovaSupported()) {
      allow_upload = self.validOptions && self.validOptions.image;
    }

    var maxFileSizeOfProperty = Number(self.properties.maxSize) || 1048576;
    var maxFileSizeOfUser = typeof BIZAGI_SETTINGS !== "undefined" && typeof BIZAGI_SETTINGS.UploadMaxFileSize !== "undefined" ? Number(BIZAGI_SETTINGS.UploadMaxFileSize) : 1048576;

    self.properties.maxSize = maxFileSizeOfProperty < maxFileSizeOfUser ? maxFileSizeOfProperty : maxFileSizeOfUser;

    // Render template
    var html = $.kendoTmpl(template, {
      xpath: bizagi.util.encodeXpath(self.getUploadXpath()),
      editable: self.properties.editable && allow_upload,
      noFiles: self.filesCount === 0,
      allow_upload: allow_upload,
      validExtensions: self.properties.validExtensions,
      nonSupportedExtension: self.nonSupportedExtension,
      allowSendInMail: self.properties.allowSendInMail });


    // Render current children        
    for (var i = 0, length = self.filesCount; i < length; i++) {
      var item = self.renderUploadItem(self.files[i], i);
      items += item;
    }

    html = self.replaceFilesHtml(html, items);
    return html;
  },

  /**
      * Render readonly
      * @returns {} 
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this._super();
    this.atachOpenFileEvent();
  },

  /**
      * Active upload by native mode
      * @returns {} 
      */
  activateUploadNative: function activateUploadNative() {
    this._super();
  },

  getTemplateName: function getTemplateName() {
    return "upload";
  },

  getTemplateItemName: function getTemplateItemName() {
    return "uploadItem";
  },

  getTemplateEditionName: function getTemplateEditionName() {
    return "edition.upload";
  },

  getTemplateEditionMenu: function getTemplateEditionMenu() {
    return "edition.upload.menu";
  },

  saveImage: function saveImage(context, dataImage) {
    var self = this;

    // Fix android 4.4 getting images from recent folder        
    dataImage = bizagi.util.media.getImagePath(dataImage);

    // Load image
    self.getDataUri(dataImage, function (dataUrl) {
      var fileExtension = self.fileProperties ? self.fileProperties.extension : "";
      var fileName = bizagi.util.uniqueID() + fileExtension;

      self.onUploadFileCompletedOffline(fileName, dataUrl);
    });
  },

  /**
      * Create an offline image
      * @param {} context 
      * @param {} url 
      * @param {} callback 
      * @returns {} 
      */
  getDataUri: function getDataUri(path, callback) {
    var self = this;
    var xhr = new XMLHttpRequest();

    try {

      xhr.onload = function () {
        var reader = new FileReader();

        reader.onloadend = function () {
          var content = reader.result;
          callback(content);
        };
        reader.readAsDataURL(xhr.response);
      };

      xhr.open("GET", path);
      xhr.responseType = "blob";
      xhr.send();

    } catch (error) {
      callback("");
    }
  },

  saveVideo: function saveVideo(context, dataVideo) {
    var self = context;

    window.resolveLocalFileSystemURL(dataVideo[0].fullPath, function (fileEntry) {
      fileEntry.file(function (file) {
        var reader = new FileReader();

        reader.onloadend = function (evt) {
          var fileExtension = bizagi.util.media.getFileExtension(dataVideo[0].name);
          var fileName = bizagi.util.uniqueID() + fileExtension;

          self.onUploadFileCompletedOffline(fileName, evt.target.result);
        };

        reader.readAsDataURL(file);
      }, self.failReadFile);
    }, self.failReadFile);
  },

  renderUploadItem: function renderUploadItem(objectUpload, index) {
    var self = this;
    var mode = self.getMode();
    var template = self.renderFactory.getTemplate("uploadItem");

    var html = $.kendoTmpl(template, {
      url: "javascript:void(0);",
      image: false,
      allowDelete: self.properties.allowDelete || false,
      id: bizagi.util.randomNumber(),
      mode: mode,
      editable: self.properties.editable,
      filename: objectUpload[0].value,
      isNativeContext: false,
      isCordovaSupported: bizagi.util.isCordovaSupported(),
      index: index });


    return html;
  },

  /**
      * Collect Data
      * @param {} renderValues 
      * @returns {} 
      */
  collectData: function collectData(renderValues) {
    var self = this;

    if (self.filesCount > 0) {
      renderValues[self.properties.xpath] = self.files;
    } else if (self.properties.method === "delete") {
      renderValues[self.properties.xpath] = [];

      delete self.properties.method;
    }
  },

  onUploadFileCompletedOffline: function onUploadFileCompletedOffline(name, dataimage) {
    var self = this;
    var control = self.getControl();
    var uploadWrapper = $("ul.files", control);
    var dataUrltoSave = dataimage.replace(/^data:.*;base64,/, "");
    var properties = self.properties;
    var dataMimeType = dataimage.match(/data:([a-zA-Z0-9]+\/[a-zA-Z0-9-.+]+).*,.*/);
    dataMimeType = dataMimeType && dataMimeType.length ? dataMimeType[1] : "image/jpeg";

    var fileToSave = [
    { xpath: "fileName", DataType: "15", value: name },
    { xpath: "data", DataType: "19", value: dataUrltoSave },
    { xpath: "mimeType", DataType: "15", value: dataMimeType }];


    // Locate it before the upload wrapper
    var newItem = self.renderUploadItem(fileToSave, self.filesCount);
    $(uploadWrapper).append(newItem);

    // Copy the files value but not its reference
    var files = self.files.slice();

    //Add the new item to this temporal array
    files.push(fileToSave);

    // Increment counter
    self.filesCount = self.filesCount + 1;

    // Update value
    self.setValue(files, false);

    // Check maxFiles
    self.checkMaxFiles();

    self.triggerRenderChange();

    control.find(".ui-bizagi-render-upload-item-no-upload").hide();

    if (properties.allowDelete && properties.editable) {
      self.attachDeleteHandler();
    }

    // Close modalview
    self.closeModalView();
    bizagi.util.tablet.stopLoading();
  },

  failReadFile: function failReadFile(evt) {
    try {
      bizagi.log("Error code: " + evt.target.error.code);
    } catch (e) {
    }
  },

  onFail: function onFail(error) {
    this._super(error);
    this.closeModalView();
  },

  checkMaxFiles: function checkMaxFiles() {
    bizagi.rendering.upload.prototype.checkMaxFiles.apply(this, arguments);
  },

  checkExtensions: function checkExtensions() {
    var self = this;
    var validExtensions = self.properties.validExtensions || "";

    //Helps to get the non-supported extensions for mobile
    var nonSupportedExtensionsArr = [];

    // Remove spaces in the list
    validExtensions = validExtensions.replace(/\s+/g, "").toLowerCase();
    validExtensions = validExtensions.split(";");

    // Initialize the nonSupportedExtension element
    self.nonSupportedExtension = "";

    self.validOptions = { video: false, image: false, file: false };

    if (typeof validExtensions === "undefined" || validExtensions[0] === "") {
      self.validOptions.image = true;

      return;
    }

    if (validExtensions.length === 1 && validExtensions[0].indexOf("*.*") !== -1) return;

    for (var index = 0, length = validExtensions.length; index < length; index++) {
      validExtensions[index] = validExtensions[index].replace("*.", "");

      if (self.Class.EXTENSIONSIMG.toString().indexOf(validExtensions[index]) >= 0) {
        self.validOptions.image = true;
      } else {
        // If the extension is not part or any category, it is stored in nonSupportedExtensions array
        nonSupportedExtensionsArr.push(validExtensions[index]);
      }
    }

    // Adjust file extension, in order to make it multiline & Stores the non-supported extensions
    self.nonSupportedExtension = nonSupportedExtensionsArr.join(", ") || "";
  },

  checkMaxSize: function checkMaxSize(objectUri) {
    return bizagi.util.media.checkMaxSize(objectUri, this.properties);
  },

  checkMaxSizeVideo: function checkMaxSizeVideo(objectVideo) {
    return bizagi.util.media.checkMaxSizeVideo.apply(objectVideo, this.properties);
  },

  /**  
      * This render cannot be sent because it is full ajax
      * @returns {} 
      */
  canBeSent: function canBeSent() {
    return true;
  },

  /**
      * Attach delete option & Removes a file from the upload control
      * @returns {} 
      */
  attachDeleteHandler: function attachDeleteHandler() {
    var self = this;
    var control = self.getControl();

    $(".bz-rn-upload-delete-icon", control).bind("click", function () {
      var context = $(this);
      var item = context.parent(".ui-bizagi-render-upload-item");
      var file = item.data();
      var fileIndex = item.index();

      // Substract counter
      self.filesCount = self.filesCount - 1;

      if (self.filesCount === 0) {
        self.properties.method = "delete";
      }

      // Remove from local collection
      self.files = $.grep(self.files, function (fileItem, index) {
        return index !== fileIndex && fileItem !== file.name;
      });

      // Remove item
      item.hide();
      item.remove();

      // Update value
      self.setValue(self.files, false);

      // Check maxFiles
      self.checkMaxFiles();

      // Trigger change
      self.triggerRenderChange();
    });
  } });