/*
 *   Name: Bizagi Tablet Render Link Extension
 *   Author: Bizagi Mobile Team
 *   Comments:
 *   -   This script will redefine the link render class to adjust to tablet devices
 */

// Extends itself
bizagi.rendering.link.extend("bizagi.rendering.link", {}, {

  /**
                                                             * Configure the controls interactions
                                                             * @returns {} 
                                                             */
  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();
    var properties = self.properties;
    var link = $(".ui-bizagi-render-link", control);

    self.input = control.find("a");

    if (properties.value) {
      $(self.input).on("click", function (event) {
        event.preventDefault();

        if (self.isValidValue(properties.value)) {
          window.open(encodeURI(this.getAttribute("href")), "_system");
        }
      });
    } else {
      link.addClass("bz-rn-link-disabled");
      control.closest(".ui-bizagi-render").addClass("bz-rn-read-only");
    }
  },

  /**
      * Checks if the value is a valid url (http/https valid urls)
      *
      * @param {String} value The control's value that is needed to be displayed
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;

    if (self.isValidValue(value)) {
      //Get the decoded version of the URL
      value = getFullyDecodedURI($.trim(value));
      self.input.attr("href", value);
      self.input.attr("target", "_blank");
      self.input.attr("data-rel", "external");
    }
  },

  /*
      *   Method to render non editable values
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;
    var properties = self.properties;

    // Execute configure handlers
    if (properties.value) {
      self.configureHandlers();
    }

    // Help Text Control
    self.configureHelpText();
  },

  /**
      * Checks if the value is a valid url (http/https valid urls)
      *
      * @param {String} value Stored value in the control
      */
  isValidValue: function isValidValue(value) {
    return value && $.trim(value + "").toLowerCase().match(/^(http|https):\/\//) !== null || false;
  } });